//–≤–µ—Ç–∫–∞ badge-slider-module

import React from "react"
import PropTypes from 'prop-types'

/**
 * Badge - –¥–ª—è –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–∏—è –∑–Ω–∞—á–µ–Ω–∏–π –∫–æ–ª–∏—á–µ—Å—Ç–≤–∞, —è—Ä–ª—ã–∫–æ–≤ –∏–ª–∏ –º–µ—Ç–æ–∫
 * @param customCss - –¥–æ–ø–æ–ª–Ω–∏—Ç–µ–ª—å–Ω—ã–µ –∫–ª–∞—Å—Å—ã –≤ —Ñ–æ—Ä–º–∞—Ç–µ string
 * @param color - —Ü–≤–µ—Ç –±–ª–æ–∫–∞ –≤ —Ñ–æ—Ä–º–∞—Ç–µ —ç–ª–µ–º–µ–Ω—Ç–∞ –º–∞—Å—Å–∏–≤–∞ badgeColors
 * @param textColor - —Ü–≤–µ—Ç —Ç–µ–∫—Å—Ç–∞ –ø–æ —É–º–æ–ª—á–∞–Ω–∏—é –±–µ–ª—ã–π –ø—Ä–∏ –ø–µ—Ä–µ–¥–∞—á–µ 'dark' —Å—Ç–∞–Ω–æ–≤–∏—Ç—Å—è —á–µ—Ä–Ω—ã–º
 * @param roundedPill   - —Å–∫—Ä—É–≥–ª–µ–Ω–∏–µ —É–≥–ª–æ–≤. –î–ª—è –±–æ–ª–µ–µ –±–æ–ª—å—à–æ–≥–æ –ø–∞—Ä–∞–º–µ—Ç—Ä–∞ –ø–µ—Ä–µ–¥–≤—Ç—å –≤ –ø–∞—Ä–∞–º–µ—Ç—Ä–µ: pill
 * @param children  - —ç–ª–µ–º–µ–Ω—Ç –æ—Ç–æ–±—Ä–∞–∂–∞–µ–º—ã–π –≤ badge
 * @returns {*}
 * @constructor
 */
const Badge = ({customCss, color, textColor, roundedPill, children}) => {
    const classBadge = [
        'badge',
        customCss ? customCss : '',
        textColor === 'dark' && 'text-dark',
        roundedPill === 'pill' && 'rounded-pill',
        `bg-${color}`
    ].join(' ')

    return (
      <span
          className={classBadge}
      >
            {children}
      </span>
    )
}

const badgeColors = ['primary', 'secondary', 'success', 'danger', 'warning', 'info', 'light', 'dark']

Badge.defaultProps = {
    color: 'primary',
    textColor: '',
    roundedPill: ''
}

Badge.propTypes = {
    customCss: PropTypes.string,
    color: PropTypes.oneOf(badgeColors),
    textColor: PropTypes.oneOf(['dark', '']),
    roundedPill: PropTypes.oneOf(['pill', '']),
    children: PropTypes.oneOfType([PropTypes.element, PropTypes.string])
}

export default Badge

